import { useEffect, useState } from "react";
import styled, { css } from "styled-components";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { InnerWrap, Text, Title2, Title3 } from "../../assets/styles/component";

const bg = css`
  content: "";
  z-index: -1;
  position: absolute;
`;

const STechnology = styled.section`
  padding: 100px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;

  & > div {
    & > p {
      text-align: center;
      margin-bottom: 70px;

      &::after {
        ${bg}
        width: 43vw;
        height: 43vw;
        left: 0;
        top: 30%;
        border-radius: 50%;
        filter: blur(150px);
        opacity: 0.4;
        transform: translate(-90%, 0);

        background: #f10275;
        mix-blend-mode: screen;
      }

      &::before {
        ${bg}
        width: 43vw;
        height: 43vw;
        left: 0;
        top: 30%;

        background: #0202f1;

        border-radius: 50%;
        filter: blur(150px);
        transform: translate(-130%, 0);

        mix-blend-mode: screen;
      }
    }

    &::after {
      ${bg}
      width: 43vw;
      height: 43vw;
      left: 0;
      top: 35%;
      border-radius: 50%;
      background: #ff429e;
      opacity: 0.1;

      filter: blur(150px);
      transform: translate(0, 0);

      mix-blend-mode: screen;
    }

    &::before {
      ${bg}
      width: 43vw;
      height: 43vw;
      right: 0;
      bottom: 0;
      border-radius: 50%;
      opacity: 0.4;
      filter: blur(150px);
      transform: translate(70%, 60%);

      background: #f10275;
      mix-blend-mode: screen;
    }
  }

  &::after {
    ${bg}
    width: 43vw;
    height: 43vw;
    right: 0;
    bottom: 0;
    border-radius: 50%;
    opacity: 0.4;
    filter: blur(150px);
    transform: translate(10%, 10%);
    z-index: -40;

    opacity: 0.3;
    background: #0202f1;
    mix-blend-mode: screen;
  }

  &::before {
    ${bg}
    width: 43vw;
    height: 43vw;
    right: 0;
    bottom: 0;
    border-radius: 50%;
    opacity: 0.4;
    filter: blur(150px);
    transform: translate(70%, 60%);

    opacity: 0.46;
    background: #ff429e;
    mix-blend-mode: screen;
  }
`;

const Title = styled(Title2)`
  text-align: center;
  margin-bottom: 20px;
  font-size: 26px;
`;

const TechnologyList = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 80px 33px;
  height: 370px;
  align-items: flex-end;
`;

const Tech = styled.div`
  position: absolute;
  top: 0;
  width: 220px;
  min-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;

  opacity: 0;
  transition: 1s ease-in-out;

  ${({ active = false }) =>
    active &&
    `
    opacity: 1;
    
  `}

  h3 {
    font-size: 18px;
  }

  p {
    margin-top: 15px;
    text-align: center;
    opacity: 0.7;
  }

  &:last-child {
    img {
      transform: translateY(-15%);
    }
  }
`;

const PointBox = styled.div`
  display: flex;
  gap: 16px;
`;

const Point = styled.div`
  width: 10px;
  height: 10px;
  border-radius: 12px;
  transition: 0.5s ease-in-out;
  background-color: #5a3be4;
  transform: scale(1);

  ${({ active = false }) =>
    active &&
    `
      transform: scale(1.1);
      background: #5A3BE4;
      box-shadow: 0px 0px 11px #8B73F7;
    `}
`;

const ImgBox = styled.div`
  height: 180px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  margin-bottom: 0px;

  img {
    max-width: 110%;
    max-height: 120%;
  }
`;

const Technology = ({ language, technologies }) => {
  const [activeTech, setActiveTech] = useState(0);


  useEffect(() => {
    // clearTimeout(Slide);

    let Slide = setTimeout(() => {
      if (activeTech < technologies.length - 1) {
        setActiveTech(activeTech + 1);
      } else {
        setActiveTech(0);
      }
    }, 3000);
  }, [activeTech]);

  return (
    <STechnology>
      <InnerWrap>
        <Title>
          Технологии <br /> дополненной реальности
        </Title>

        <Text fix_size="12" size="12">
          Объединенные технологии AR и AI <br /> позволяют создавать уникальный{" "}
          <br />
          контент прямо на трансляции стримера
        </Text>

        <TechnologyList>
          {technologies.map((technology, index) => (
            <Tech key={index} active={index == activeTech}>
              <ImgBox>
                <LazyLoadImage src={technology.img} alt="technology" loading="lazy" />
              </ImgBox>
              <Title3>{technology.title}</Title3>
              <Text fix_size="10" size="10">
                {technology.text}
              </Text>
            </Tech>
          ))}

          <PointBox>
            {technologies.map((tech, index) => (
              <Point onClick={()=> setActiveTech(index)} key={index} active={index == activeTech}></Point>
            ))}
          </PointBox>
        </TechnologyList>
      </InnerWrap>
    </STechnology>
  );
};

export default Technology;
